import HotUpdatePanel from './HotUpdatePanel';

const { ccclass, property } = cc._decorator;

/**
 * 
 * @param versionA A
 * @param versionB B
 * @returns AB, 1; AB, -1; AB, 0
 */
function versionCompareHandle(versionA: string, versionB: string): number {
    cc.log(
        ': version A is ' +
            versionA +
            ', version B is ' +
            versionB
    );
    const vA = versionA.split('.');
    const vB = versionB.split('.');
    for (let i = 0; i < vA.length; ++i) {
        const a = parseInt(vA[i]);
        const b = parseInt(vB[i] || '0');
        if (a === b) {
            continue;
        } else {
            return a - b;
        }
    }
    if (vB.length > vA.length) {
        return -1;
    } else {
        return 0;
    }
}

@ccclass
export default class HotUpdateMgr extends cc.Component {
    @property(cc.Asset)
    manifestUrl: cc.Asset = null;

    @property(HotUpdatePanel)
    hotUpdatePanel: HotUpdatePanel = null;

    // 
    _am: jsb.AssetsManager = null;
    // 
    _storagePath: string = '';
    // 
    _updating: boolean = false;
    // 
    _canRetry: boolean = false;
    // 
    _failCount: number = 0;

    /**
     * , 
     */
    protected onLoad(): void {
        if (!cc.sys.isNative) {
            this.hotUpdatePanel.node.active = false;
            return;
        }
        // , 
        this._storagePath =
            (jsb.fileUtils ? jsb.fileUtils.getWritablePath() : '/') +
            'match-3-remote-asset';
        cc.log(': ' + this._storagePath);
        // 
        this._am = new jsb.AssetsManager(
            '',
            this._storagePath,
            versionCompareHandle
        );

        // 
        this._am.setVerifyCallback((path, asset) => {
            // 
            const compressed = asset.compressed;
            // md5
            const expectedMD5 = asset.md5;
            // 
            const relativePath = asset.path;
            // 
            //const size = asset.size;
            if (compressed) {
                this.hotUpdatePanel.setInfo(': ' + relativePath);
                return true;
            } else {
                this.hotUpdatePanel.setInfo(
                    ': ' + relativePath + ' (' + expectedMD5 + ')'
                );
                return true;
            }
        });

        this.hotUpdatePanel.setInfo('');

        if (cc.sys.os === cc.sys.OS_ANDROID) {
            // Android
            // 
            // 2
            (this._am as any).setMaxConcurrentTask &&
                (this._am as any).setMaxConcurrentTask(2);
            this.hotUpdatePanel.setInfo('2');
        }

        // 
        this.hotUpdatePanel.setFileProgress(0);
        this.hotUpdatePanel.setByteProgress(0);
    }

    private checkCb(event: jsb.EventAssetsManager): void {
        cc.log(' Code: ' + event.getEventCode());
        switch (event.getEventCode()) {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                // 
                this.hotUpdatePanel.setInfo(
                    '...'
                );
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
                // 
                this.hotUpdatePanel.setInfo('...');
                break;
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                // 
                this.hotUpdatePanel.setInfo('...');
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:
                // 
                this.hotUpdatePanel.setInfo('...');
                break;
            case jsb.EventAssetsManager.NEW_VERSION_FOUND:
                // 
                this.hotUpdatePanel.setInfo(
                    '... (: ' +
                        this._am.getTotalBytes() +
                        'bytes)'
                );
                // 
                this.hotUpdatePanel.setCheckBtnActive(false);
                // 0
                this.hotUpdatePanel.setFileProgress(0);
                // 0
                this.hotUpdatePanel.setByteProgress(0);
                break;
            default:
                return;
        }
        // 
        this._am.setEventCallback(null);
        // false
        this._updating = false;
    }

    /**
     * 
     */
    public checkUpdate(): void {
        if (this._updating) {
            this.hotUpdatePanel.setInfo('...');
            return;
        }
        // 
        if (this._am.getState() === jsb.AssetsManager.State.UNINITED) {
            // md5 url
            let url = this.manifestUrl.nativeUrl;
            if (cc.loader.md5Pipe) {
                // md5url
                url = cc.loader.md5Pipe.transformURL(url);
            }
            // 
            this._am.loadLocalManifest(url);
        }
        // 
        if (
            !this._am.getLocalManifest() ||
            !this._am.getLocalManifest().isLoaded()
        ) {
            this.hotUpdatePanel.setInfo('...');
            return;
        }
        // 
        this._am.setEventCallback(this.checkCb.bind(this));
        // 
        this._am.checkUpdate();
        // true
        this._updating = true;
    }

    private updateCb(event: jsb.EventAssetsManager): void {
        var needRestart = false;
        var failed = false;
        switch (event.getEventCode()) {
            case jsb.EventAssetsManager.ERROR_NO_LOCAL_MANIFEST:
                this.hotUpdatePanel.setInfo(
                    '...'
                );
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_PROGRESSION:
                // 
                this.hotUpdatePanel.setByteProgress(event.getPercent());
                // 
                this.hotUpdatePanel.setFileProgress(event.getPercentByFile());
                // :  / 
                this.hotUpdatePanel.setFileLabel(
                    event.getDownloadedFiles() + ' / ' + event.getTotalFiles()
                );
                // :  / 
                this.hotUpdatePanel.setByteLabel(
                    event.getDownloadedBytes() + ' / ' + event.getTotalBytes()
                );
                // : 
                var msg = event.getMessage();
                if (msg) {
                    this.hotUpdatePanel.setInfo('Updated file: ' + msg);
                }
                break;
            case jsb.EventAssetsManager.ERROR_DOWNLOAD_MANIFEST:
            case jsb.EventAssetsManager.ERROR_PARSE_MANIFEST:
                // 
                this.hotUpdatePanel.setInfo(
                    '...'
                );
                failed = true;
                break;
            case jsb.EventAssetsManager.ALREADY_UP_TO_DATE:
                // 
                this.hotUpdatePanel.setInfo('...');
                failed = true;
                break;
            case jsb.EventAssetsManager.UPDATE_FINISHED:
                // 
                this.hotUpdatePanel.setInfo(
                    '... ' + event.getMessage()
                );
                // true
                needRestart = true;
                break;
            case jsb.EventAssetsManager.UPDATE_FAILED:
                // 
                this.hotUpdatePanel.setInfo(
                    '... ' + event.getMessage()
                );
                // 
                this.hotUpdatePanel.setRetryBtnActive(true);
                // false
                this._updating = false;
                // true
                this._canRetry = true;
                break;
            case jsb.EventAssetsManager.ERROR_UPDATING:
                // 
                this.hotUpdatePanel.setInfo(
                    ': ' +
                        event.getAssetId() +
                        ', ' +
                        event.getMessage()
                );
                break;
            case jsb.EventAssetsManager.ERROR_DECOMPRESS:
                // 
                this.hotUpdatePanel.setInfo(': ' + event.getMessage());
                break;
            default:
                break;
        }

        if (failed) {
            // 
            this._am.setEventCallback(null);
            this._updating = false;
        }

        // 
        if (needRestart) {
            // 
            this._am.setEventCallback(null);
            // 
            var searchPaths = jsb.fileUtils.getSearchPaths();
            // storagePath manifest  searchPaths  storagePath 
            var newPaths = this._am.getLocalManifest().getSearchPaths();
            // 
            for (var i = 0; i < newPaths.length; i++) {
                if (searchPaths.indexOf(newPaths[i]) == -1) {
                    Array.prototype.unshift.apply(searchPaths, [newPaths[i]]);
                }
            }
            // 
            // This value will be retrieved and appended to the default search path during game startup,
            // please refer to samples/js-tests/main.js for detailed usage.
            // !!! Re-add the search paths in main.js is very important, otherwise, new scripts won't take effect.
            cc.sys.localStorage.setItem(
                'HotUpdateSearchPaths',
                JSON.stringify(searchPaths)
            );
            // 
            jsb.fileUtils.setSearchPaths(searchPaths);
            // 
            cc.audioEngine.stopAll();
            // 
            cc.game.restart();
        }
    }

    /**
     * 
     */
    public hotUpdate(): void {
        if (this._am && !this._updating) {
            // 
            this._am.setEventCallback(this.updateCb.bind(this));
            // 
            if (this._am.getState() === jsb.AssetsManager.State.UNINITED) {
                // Resolve md5 url
                var url = this.manifestUrl.nativeUrl;
                if (cc.loader.md5Pipe) {
                    // md5url
                    url = cc.loader.md5Pipe.transformURL(url);
                }
                // 
                this._am.loadLocalManifest(url);
            }

            // 
            this._failCount = 0;
            // 
            this._am.update();
            // 
            this.hotUpdatePanel.setUpdateBtnActive(false);
            // true
            this._updating = true;
        }
    }

    public retry(): void {
        if (!this._updating && this._canRetry) {
            this.hotUpdatePanel.setRetryBtnActive(false);
            this._canRetry = false;
            // : 
            this.hotUpdatePanel.setInfo('...');
            this._am.downloadFailedAssets();
        }
    }

    public showUpdateUI(): void {
        this.hotUpdatePanel.node.active = true;
    }

    protected onDestroy(): void {
        this._am && this._am.setEventCallback(null);
    }
}
