/**
 * 
 * 
 */
export enum GameState {
    /**  */
    READY = 'ready',
    /**  */
    PLAYING = 'playing',
    /**  */
    CHECKING = 'checking',
    /** , */
    ELIMINATING = 'eliminating',
    /**  */
    DROPPING = 'dropping',
    /**  */
    GAME_OVER = 'gameOver',
    /**  */
    GAME_WIN = 'gameWin',
}

/**
 * 
 */
export type StateChangeCallback = (
    fromState: GameState,
    toState: GameState
) => void;

/**
 * 
 * 
 */
export default class GameFsm {
    /**  */
    private _currentState: GameState;

    /**  */
    private _stateChangeCallbacks: StateChangeCallback[] = [];

    /**  -  */
    private _stateTransitions: Map<GameState, GameState[]>;

    constructor(initialState: GameState = GameState.READY) {
        this._currentState = initialState;
        this._stateTransitions = new Map();
        this._initStateTransitions();
    }

    /**
     * 
     * 
     */
    private _initStateTransitions(): void {
        // Ready  Playing
        this._stateTransitions.set(GameState.READY, [GameState.PLAYING]);

        // Playing  CheckingGameOverGameWin
        this._stateTransitions.set(GameState.PLAYING, [
            GameState.CHECKING,
            GameState.GAME_OVER,
            GameState.GAME_WIN,
        ]);

        // Checking  PlayingEliminatingGameOverGameWin
        this._stateTransitions.set(GameState.CHECKING, [
            GameState.PLAYING,
            GameState.ELIMINATING,
            GameState.GAME_OVER,
            GameState.GAME_WIN,
        ]);

        // Eliminating  DroppingGameOverGameWin
        this._stateTransitions.set(GameState.ELIMINATING, [
            GameState.DROPPING,
            GameState.GAME_OVER,
            GameState.GAME_WIN,
        ]);

        // Dropping  CheckingGameOverGameWin
        this._stateTransitions.set(GameState.DROPPING, [
            GameState.CHECKING,
            GameState.GAME_OVER,
            GameState.GAME_WIN,
        ]);

        // GameOver  GameWin  Ready ()
        this._stateTransitions.set(GameState.GAME_OVER, [GameState.READY]);
        this._stateTransitions.set(GameState.GAME_WIN, [GameState.READY]);
    }

    /**
     * 
     */
    public get currentState(): GameState {
        return this._currentState;
    }

    /**
     * 
     * @param toState 
     * @returns 
     */
    public canTransitionTo(toState: GameState): boolean {
        const allowedStates = this._stateTransitions.get(this._currentState);
        return allowedStates ? allowedStates.includes(toState) : false;
    }

    /**
     * 
     * @param newState 
     * @param force 
     * @returns 
     */
    public changeState(newState: GameState, force: boolean = false): boolean {
        // 
        if (this._currentState === newState) {
            console.warn(`[GameFsm] : ${newState}`);
            return false;
        }

        // 
        if (!force && !this.canTransitionTo(newState)) {
            console.warn(
                `[GameFsm]  ${this._currentState}  ${newState}`
            );
            return false;
        }

        const oldState = this._currentState;
        this._currentState = newState;

        console.log(`[GameFsm] : ${oldState} -> ${newState}`);

        // 
        this._notifyStateChange(oldState, newState);

        return true;
    }

    /**
     * 
     * @param callback 
     */
    public onStateChange(callback: StateChangeCallback): void {
        if (!this._stateChangeCallbacks.includes(callback)) {
            this._stateChangeCallbacks.push(callback);
        }
    }

    /**
     * 
     * @param callback 
     */
    public offStateChange(callback: StateChangeCallback): void {
        const index = this._stateChangeCallbacks.indexOf(callback);
        if (index !== -1) {
            this._stateChangeCallbacks.splice(index, 1);
        }
    }

    /**
     * 
     * @param fromState 
     * @param toState 
     */
    private _notifyStateChange(fromState: GameState, toState: GameState): void {
        this._stateChangeCallbacks.forEach((callback) => {
            try {
                callback(fromState, toState);
            } catch (error) {
                console.error('[GameFsm] :', error);
            }
        });
    }

    /**
     * 
     * @param state 
     */
    public isState(state: GameState): boolean {
        return this._currentState === state;
    }

    /**
     * 
     */
    public isGameActive(): boolean {
        return (
            this._currentState !== GameState.GAME_OVER &&
            this._currentState !== GameState.GAME_WIN &&
            this._currentState !== GameState.READY
        );
    }

    /**
     * 
     */
    public canPlayerOperate(): boolean {
        return this._currentState === GameState.PLAYING;
    }

    /**
     * 
     */
    public reset(): void {
        const oldState = this._currentState;
        this._currentState = GameState.READY;
        console.log(`[GameFsm] : ${GameState.READY}`);
        this._notifyStateChange(oldState, GameState.READY);
    }

    /**
     * 
     */
    public destroy(): void {
        this._stateChangeCallbacks = [];
        console.log('[GameFsm] ');
    }
}
