import BaseBlock from './block/BaseBlock';

export type Location = { row: number; column: number };

// 
export enum BlockType {
    BLOCK_TYPE_1 = 1,
    BLOCK_TYPE_2 = 2,
    BLOCK_TYPE_3 = 3,
    BLOCK_TYPE_4 = 4,
    BLOCK_TYPE_5 = 5,
}

// 
export enum BlockSpecialType {
    NONE = 'none', // 
    ROW = 'row', // 
    COLUMN = 'column', // 
    BOOM = 'boom', // 
    SPECIAL_BOOM = 'special_boom', // 
}

// 
export enum EliminateType {
    BASE_3 = 'base_3', // 3
    BASE_4_ROW = 'base_4_row', // 4
    BASE_4_COLUMN = 'base_4_column', // 4
    BASE_5 = 'base_5', // 5
    IRREGULAR_5 = 'irregular_5', // 5
}

export enum EliminateScore {
    EliminateBlockScore = 10, // 
    Base4RowScore = 50, // 4
    Base4ColumnScore = 50, // 4
    Base5Score = 200, // 5
    Irregular5Score = 100, // 5
}

export interface UserLevelInfo {
    maxLevelId: number; // ID
}

// 
export interface GameConfig {
    level: string;
    levelConfig: LevelConfig;
}

// 
export interface LevelConfig {
    id: number; // ID
    stepLimit: number; // 
    targetScore: number; // 
    gameMap: GameMap; // 
}

// 
export interface GameMap {
    width: number; // 
    height: number; // 
    blocks: Record<string, BlockConfig>; // 
}

// 
export interface BlockConfig {
    type: BlockType; // 
    special: BlockSpecialType; // 
}

/**
 * 
 * @param gameMapInfo 
 * @param checkBlockEntry 
 * @param swapBlocks 
 * @param eliminateScore 
 * @param canEliminateCheckInfos 
 * @param newSpecailBlocks 
 */
export interface GameCheckInfo {
    gameMapInfo: Array<Array<BlockConfig>>; // 
    checkBlockEntry: Location[]; // 
    swapBlocks: [Location, Location] | null; // ,  null
    chainCount: number; // 
    eliminateScore: number; // 
    canEliminateCheckInfos: Array<EliminateCheckInfo>; // 
    newSpecailBlocks: Record<string, BlockConfig>; // 
}

/**
 * 
 * @param entryLocation 
 * @param eliminateBlockType 
 * @param contiguousLocations 
 */
export interface EliminateCheckInfo {
    entryLocation: Location; // 
    eliminateBlockType: BlockSpecialType; // 
    contiguousLocations: Array<Location>; // 
}

/**
 * 
 * @param location 
 * @param blockConfig 
 */
export interface EliminateBlockInfo {
    location: Location; // 
    blockConfig: BlockConfig; // 
}

/**
 * 
 * @param location 
 * @param block 
 * @param dropTime 
 * @param targetLocation 
 */
export interface BlockDropInfo {
    location: Location; // 
    block: BaseBlock; // 
    dropTime: number; // 
    targetDropLocation: Location; // 
}
